import React from "react";
import { Card, Icon, Avatar, Button } from "antd";
import { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useCallback } from "react";

const PostCard = ({ post }) => {
    const [commentFormOpened, setcommentFormOpened] = useState(false);
    const [commentText, setcommentText] = useState("");
    const { me } = useSelector((state) => state.user);
    const dispatch = useDispatch();

    // 댓글 아이콘 누르면 펼쳐지고 닫히고
    const onToggleComment = useCallback(() => {
        setcommentFormOpened((prev) => !prev);
    }, []);

    return (
        <Card
            key={+post.createdAt}
            cover={post.img && <img alt="example" src={post.img} />}
            actions={[<Icon type="retweet" key="retweet"></Icon>, <Icon type="heart" key="heart"></Icon>, <Icon type="message" key="message"></Icon>, <Icon type="ellipsis" key="ellipsis"></Icon>]}
            extra={<Button>팔로우</Button>}
        >
            <Card.Meta avatar={<Avatar>{post.User.nickname[0]}</Avatar>} title={post.User.nickname} description={post.content} />
        </Card>
    );
};

export default PostCard;
